.AboutMe {
  display: flex;
  align-items: center;
  justify-content: center;

  &__socialLinksContent {
    margin: var(--default-padding) 0;
    display: flex;
  }

  &__body {
    direction: var(--default-direction);
  }

  &__icon {
    animation: shine 2s infinite alternate, rotate 10s infinite linear;
  }

  &__burst {
    position: absolute;
    pointer-events: none;
  }

  @keyframes shine {
    from {
      filter: drop-shadow(0 0 0 #F6E05E);
    }
    to {
      filter: drop-shadow(0 0 8px #F6E05E);
    }
  }

  @keyframes rotate {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
}
