.modal {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all var(--animation-timing);

    &__overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: var(--color-dark1);
        opacity: .5;
    }

    &__content {
        position: relative;
        border-radius: var(--default-border-radius);
        background-color: var(--color-dark1-theme);
        min-width: 400px;
        max-width: 600px;
        min-height: 200px;
        max-height: 80vh;
        color: var(--color-light1-theme);
        display: flex;
        flex-direction: column;
    }

    &__close {
        display: flex;
        position: absolute;
        right: 0;
        padding: calc(var(--default-padding) + 2px) var(--default-padding) var(--default-padding);
        font-size: 20px;
        cursor: pointer;
    }

    &__title {
        text-align: center;
        padding: var(--default-padding);
        font-size: var(--font-size-large);
        font-weight: var(--font-weight-semi-bold);
        border-bottom: 1px solid var(--color-border-theme);
    }
}
