@import "../../mixins";

.login {
  color: var(--color-light1);

  &__label {
    @include buttonStyle;
    cursor: pointer;
  }

  &__data {
    padding: var(--default-padding);
  }

  &__username {
    font-size: var(--font-size-large);
  }
}

.firebaseui {
  &-container {
    background: transparent !important;
    box-shadow: none !important;
  }

  &-title {
    color: var(--color-light1-theme) !important;
  }

  &-label {
    color: var(--color-light1-theme) !important;
  }

  &-input {
    border-color: var(--color-light1-theme) !important;
    color: var(--color-light1-theme) !important;
  }
}
