@import "../../mixins";

.tafseer {
  font-family: var(--ar-font-family);

  &__tabs {
    display: flex;
    font-family: var(--ar-font-family);
  }

  &__tab {
    @include buttonStyle;
    flex: 1;
    text-align: center;
    padding: var(--default-padding);
    cursor: pointer;
    border-bottom: 1px solid var(--color-border-theme);
  }

  &__tab--isActive {
    border-bottom-color: var(--color-light1-theme);
    font-weight: var(--font-weight-semi-bold);
    /*color: var(--color-dominant);*/
  }

  &__tabContent {
    flex: 1;
    position: relative;
    padding-top: var(--default-padding);
    direction: rtl;
    font-weight: var(--font-weight-semi-bold);
    font-family: var(--ar-font-family);
    overflow: auto;
  }

  &__tabContent--isLoading:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: var(--color-dark1);
    opacity: .5;
  }

  &__tabContent--isLoading:after {
    content: "";
    position: absolute;
    width: 25px;
    height: 25px;
    top: calc(50% - 12.5px);
    left: calc(50% - 12.5px);
    border: 2px solid #fff;
    border-bottom-color: transparent;
    border-radius: 50%;
    z-index: 2;
    animation: spin infinite .75s linear;
  }
}

@keyframes spin {
  from {
    transform: rotate(0)
  }

  to {
    transform: rotate(360deg)
  }
}
