.tabsTitles {
    border-right: 1px solid var(--color-border-theme);
    overflow: auto;
}

.tabsTitles__tab {
    text-transform: capitalize;
    padding: var(--default-padding) calc(var(--default-padding) / 2);
    cursor: pointer;
}

.tabsTitles__tab--isActive {
    background-color: var(--color-border-theme);
}

[dir="rtl"] .tabsTitles {
    border-left: 1px solid var(--color-border-theme);
    border-right: none;
}

