.settings {
  position: relative;
  font-size: 20px;
  color: var(--color-light1);

  &__icon {
    position: relative;
    display: flex;
    cursor: pointer;
  }

  &--hasUpdate:before {
    content: "";
    position: absolute;
    top: 8px;
    right: 8px;
    background-color: var(--color-green-light);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(0, 0, 0, .7);
    z-index: 3;
  }

  &--isActive {
    z-index: 1;
  }
}
