.Clock {
  position: relative;
  font-size: 80px;
  font-weight: var(--font-weight-bold);
  line-height: 80px;
  margin-bottom: var(--default-padding);
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
  color: var(--color-light1);

  &__text {
    line-height: 30px;
    font-size: 30px;
    font-weight: var(--font-weight-semi-bold);
  }
}
