@import "../../mixins";

.verseContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--color-light1);

  &__text {
    border-radius: var(--default-border-radius);
    background-color: var(--color-dark1-opacity-45);
    padding: var(--default-padding);
    backdrop-filter: blur(var(--default-blur));
    text-align: center;
    min-width: 400px;
    max-width: calc(100vw - 280px);
    max-height: 50vh;
    overflow: auto;
    font-size: 20px;
    line-height: 35px;
  }

  &__text--ar {
    direction: rtl;
    font-size: 30px;
    line-height: 45px;
    font-family: var(--verse-font-family);
    font-weight: bold;
  }

  &__iconsContent {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: calc(var(--default-padding) / 2);
  }
}
