.favourites {
  display: flex;
  justify-content: flex-end;
  color: var(--color-light1);

  &__content {
    display: flex;
    min-width: 400px;
  }

  &__empty {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    color: var(--color-light3);
  }

  &__tabsContent {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    overflow: auto;
    align-items: center;
    width: 350px;
  }

  &__tabContent {
    padding: calc(var(--default-padding) / 2);
    width: 100%;
    white-space: normal;
  }

  &__tabContent {
    border-bottom: 1px solid var(--color-border-theme);
    cursor: pointer;

    &:hover {
      background: rgba(255, 255, 255, 0.08);

      .lightTheme & {
        background: rgba(0, 0, 0, 0.08);

      }
    }
  }

  &__tabContent--ar {
    text-align: right;
    font-family: var(--verse-font-family);
    font-size: 22px;
  }
}
