.prayerTimes {
  position: fixed;
  border-radius: 6px;
  padding: var(--default-padding) 0;
  background: var(--color-dark1-opacity-45);
  backdrop-filter: blur(2px);
  right: var(--default-padding);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--color-light1);

  &__row {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 100px;
    user-select: none;
  }

  &__row:before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    background-color: var(--color-light3);
  }

  &__row:not(:last-child) {
    margin-bottom: var(--default-padding);
  }

  &__row--active {
    position: relative;
    padding: 0 18px 10px;
    //border-radius: 3px;
    //background-color: rgba(0, 0, 0, 0.5);
    font-weight: 600;
    overflow: hidden;
  }

  &__row--active &__remainingLine {
    opacity: 1;
    visibility: visible;
  }

  &__row--active:before {
    opacity: 1;
    visibility: visible;
  }

  &__row--active:hover &__data {
    opacity: 0;
    visibility: hidden;
    transform: translateY(30px);
  }

  &__row--active:hover &__remaining {
    bottom: 10px;
    opacity: 1;
    visibility: visible;
  }

  &__row--done:not(&__row--active) &__title,
  &__row--done:not(&__row--active) &__value {
    text-decoration: line-through;
  }

  &__data {
    text-align: center;
    transition: all var(--animation-timing);
  }

  &__title {
    position: relative;
    font-size: var(--font-size-regular);
    text-transform: capitalize;
    font-weight: var(--font-weight-bold);
    line-height: var(--font-size-regular);
  }

  &__value {
    position: relative;
    font-size: var(--font-size-regular);
    font-weight: var(--font-weight-semi-bold);
    margin-top: calc(var(--default-padding) / 2);
    line-height: var(--font-size-regular);
  }

  &__remaining {
    position: absolute;
    bottom: -30px;
    opacity: 0;
    visibility: hidden;
    transition: all var(--animation-timing);
    color: var(--color-green-light);
    text-align: center;
    line-height: 16px;
  }

  &__remainingTime {
    margin-top: calc(var(--default-padding) / 2);
  }

  &__remainingLine {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 3px;
    width: 70%;
    background-color: var(--color-green-light);
    opacity: 0;
    visibility: hidden;
    transition: all var(--animation-timing);

    &--red {
      background-color: var(--color-red);
    }
  }

  &__soundIcon {
    cursor: pointer;
  }
}

.calendar-icon {
  width: 30px;
  cursor: pointer;
}

.audio-icon {
  width: 20px;
  cursor: pointer;
}
