.popupMenu {
  position: absolute;
  display: flex;
  opacity: 0;
  visibility: hidden;
  transition: all var(--animation-timing);

  &:after {
    content: "";
    border: 8px solid transparent;
    border-top-color: var(--color-dark1-theme);
    position: absolute;
    bottom: 0;
    margin-left: calc(13px * calc(1 - var(--ar-isActive)));
    margin-right: calc(13px * var(--ar-isActive));
  }

  &--isRight {
    &:after {
      margin-left: calc(calc(100% - 8px - 13px) * calc(1 - var(--ar-isActive)));
      margin-right: calc(calc(100% - 8px - 13px) * var(--ar-isActive));
    }
  }

  &--isActive {
    opacity: 1;
    visibility: visible;
  }

  &__bottom {
    bottom: 100%;
  }

  &__top {
    top: 100%;
  }

  &__overlay {
    position: fixed;
    background-color: var(--color-dark1);
    opacity: .5;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
  }

  &__body {
    display: flex;
    position: relative;
    font-size: var(--font-size-small);
    background-color: var(--color-dark1-theme);
    border-radius: var(--default-border-radius);
    color: var(--color-light1-theme);
    box-shadow: 1px 1px 3px rgba(0, 0, 0, .5);
    white-space: nowrap;
    height: 330px;
    margin-bottom: 16px;
    margin-left: calc(-10px * calc(1 - var(--ar-isActive)));
    margin-right: calc(-10px * var(--ar-isActive));
    min-width: 400px;
    overflow: hidden;
  }

  &__titles {
    padding: calc(var(--default-padding) / 2) 0;
    border-right: 1px solid var(--color-border-theme);
    min-width: 150px;
  }

  &__title {
    color: var(--color-light3);
    padding: calc(var(--default-padding) / 2) var(--default-padding);
    cursor: pointer;
  }

  &__title--isActive {
    color: var(--color-light1-theme);
    font-weight: var(--font-weight-bold);
  }

  &__title:hover {
    color: var(--color-light1-theme);
  }

  &__tabsContent {
    display: flex;
    width: 400px;
    flex: 1;
  }

  &__tabContent {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow: auto;
    align-items: center;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: calc(var(--default-padding) / 2);
    width: 100%;
    transition: opacity var(--animation-timing);
    text-transform: capitalize;
  }

  &__column {
    display: flex;
    flex-direction: column;
    padding: calc(var(--default-padding) / 2);
    width: 100%;
    transition: opacity var(--animation-timing);
  }

  &__row--disabled {
    opacity: .3;
    pointer-events: none;
  }

  &__row:not(:last-of-type) {
    border-bottom: 1px solid var(--color-border-theme);
  }

  &__selectContainer--fullWidth {
    flex: 1;
  }

  /*WhatsNew Tab*/
  &__whatsNewContent {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__whatsNewDate {
    font-size: var(--font-size-regular);
    margin-bottom: calc(var(--default-padding) / 2);
  }

  &__whatsNewChanges {
    width: 100%;
  }

  &__whatsNewChangesRow {
    display: flex;
    justify-content: space-between;
    font-weight: var(--font-weight-normal);
    text-transform: initial;

    &:before {
      content: "•";
      padding-right: 8px;
    }
  }

  &__whatsNewChangesRow:not(:last-of-type) {
    margin-bottom: calc(var(--default-padding) / 1.5);
  }

  &__whatsNewValue {
    white-space: initial;
    font-size: var(--font-size-small);
    margin-right: auto;
  }

  &__whatsNewType {
    text-transform: capitalize;
    align-self: flex-start;
    font-size: var(--font-size-xSmall);
    padding: calc(var(--default-padding) / 4) calc(var(--default-padding) / 2);
    border-radius: 4px;
    color: var(--color-light1);
    font-weight: var(--font-weight-semi-bold);
    margin-left: calc(var(--default-padding) / 2);
  }

  &__whatsNewType--added {
    background-color: var(--color-dominant);
  }

  &__whatsNewType--fixed {
    background-color: var(--color-blue);
  }

  &__whatsNewType--changed {
    background-color: var(--color-dark3);
  }

  &__whatsNewType--upcoming {
    background-color: #f07741;
  }

  &__whatsNewType--removed {
    background-color: #ff2b2b;
  }

  /*Hotkeys Tab*/
  &__hotKeyTitle {
    border-radius: 3px;
    background-color: var(--color-border-theme);
    padding: calc(var(--default-padding) / 5) calc(var(--default-padding) / 3);
    font-size: var(--font-size-xSmall);
    font-weight: var(--font-weight-bold);
  }

  /* Contact Tab  */
  &__tabContent--contact {
    justify-content: space-evenly;
  }

  &__logo {
    width: 90px;
    height: 90px;
  }

  &__socialLinks {
    display: flex;
    justify-content: space-evenly;
    padding: 0 calc(var(--default-padding) / 3);
    width: calc(100% - calc(var(--default-padding)));
    border-bottom: 1px solid var(--color-border);
    border-top: 1px solid var(--color-border);
    margin: 0 var(--default-padding);
  }

  &__socialLinks a {
    display: flex;
    color: var(--color-light3);
    font-size: var(--font-size-xSmall);
    font-weight: var(--font-weight-semi-bold);
    text-decoration: none;
    padding: calc(var(--default-padding) / 3) 0;
  }

  &__socialLinks a:hover {
    text-decoration: underline;
  }

  &__storeLinks {
    display: flex;
    align-items: center;
    justify-content: center;
    width: calc(100% - calc(var(--default-padding)));
    padding: 0 var(--default-padding);

    a {
      margin: 0 calc(var(--default-padding) / 2);
    }
  }

  &__photoCredit {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__photoCreditText {
    margin-bottom: calc(var(--default-padding) / 3);
    color: var(--color-light3);
    font-size: var(--font-size-xSmall);
  }

  &__photoCreditLink {
    display: flex;
    text-decoration: none;
    color: var(--color-light3);
  }
}

/*Arabic css*/
[dir="rtl"] {
  .popupMenu__body {
    direction: rtl;
    height: 385px;
  }

  .popupMenu__selectContainer {
    transform-origin: left;
  }

  .popupMenu__titles {
    border-left: 1px solid var(--color-border-theme);
    border-right: none;
  }
}

