.Header {
  position: absolute;
  display: flex;
  top: 0;
  font-weight: var(--font-weight-bold);
  padding: calc(var(--default-padding) / 2);
  width: 100%;
  z-index: 1;

  &__topRight {
    display: flex;
    align-items: center;
    backdrop-filter: blur(var(--default-blur));
    margin-left: auto;
  }
}
